.homepage {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  overflow: hidden;

  min-height: 100vh;
  background-color: #FFEEF2;
}

.headerMain {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;

  min-height: 10vh;
  background-color: #FFC8FB;
}

.headerTitle {
  color: black;
  font-family: Futura W01,Helvetica,Arial,Sans-serif;
  -webkit-text-fill-color: rgba(255,255,255,.7);
  -webkit-text-stroke-color: #595758;
  
  font-size: 4rem;
  margin: 0;
}

